body {
  margin: 0;
  padding: 0;
  font-family: "Roboto",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}
.nopadding {
  padding: 0 0 0 0 !important;
}
html {
  font-size: 16px;
}
@media (min-width: 1200px) {
  .padding {
    padding: 0 5% 0 5%;
  }
}

